import React from "react";
import * as PropTypes from "prop-types";
import Link from "gatsby-link";

const propTypes = {
  data: PropTypes.object.isRequired
};

class campaignTemplate extends React.Component {
  render() {
    const  campaign  = this.props.data.allcampagin;
    console.log("campagin"+campaign); 
    return (
      

<div style={{ marginBottom: `5rem` }}>
<section className="campagin" style={{ textAlign: `center` }}>
  <h2>
   {campaign.namespace} 
  </h2>
  <nav>
    <ul
      style={{
        listStyle: `none`,
        width: `100%`
      }}
    > 
        <li> 
          
           
           {campaign.contents.map(({ identity }, i) => (
          <div>  
           
           <Link
           to={`/assignments/` +  btoa("assignment:"+identity)}
           style={{
             textDecoration: `none`
           }}
           >  {identity}</Link>
          </div>
            ))}
       
        </li> 
    </ul>
  </nav>
</section>

</div>
    );
  }
}

campaignTemplate.propTypes = propTypes;

export default campaignTemplate;

export const CampaignPageQuery = graphql`
  query getCampaignBYId($id: String!) {
   allcampagin: campaignNameSpace(id: { eq: $id }) {
      id
  namespace
  contents{
    id
    name
    identity
  }
    }
  }
`;



